
/* HEADER */
#header {
    .search-bar-search {
        display: flex;
        justify-content: flex-end;
        
        .header-search-mock {
            width: 40%;
            background: $white;
            height: 46px;
        }
        .header-search-button-mock {
            height: 46px;
        }
    }
    .myBusiness-link-wrapper {
        
        &.mobile {
            display: none;
        }
        button {
            background: $purple-dark;
            border:solid 3px $purple-dark;
            border: solid 2px $purple-light;
            width: 100%;
            margin-left: 16px;
            border-radius: 6px;
            padding: 8px 16px;
            height: 48px !important;
                    
            a {
                color: $white;
                padding: 0;
            }
            svg {
                height: 20px;
                width: 20px;
                margin-left: 16px;
                margin: 0 0 0 8px;
            }
        }
    }
    
    @media(max-width: 768px) {
        .header-search-mock {
            display: none;
        }

        .myBusiness-link-wrapper {

            display: none;

            &.mobile {
                display: block;
                
                button {
                    padding: 6px;
                    border: none;
                    border-radius: 0px !important;

                    svg {
                        display: none;
                    }
                }
            }
        }
    }
}

/* ANCHOR MENU */
.anchor-menu .sticky-container ul li .active-sticky {
    font-weight: 600 !important;
    color: #254f90;
    font-size: 14px;
    line-height: 20px;
    text-decoration: none;
    padding-left: 16px;
    border: none;
    border-left: #b3c2d8 solid 4px;
}

/* MODAL */
.modal-wrapper {
    display: none;
}
.modal-wrapper.active {
    display: block;
    position: fixed;
    width: 500px;
    padding: 64px 32px 32px 32px;
    top: 30%;
    left: calc(50% - 250px);
    background: $white;
    z-index: 7000;
    box-shadow: 0 .125rem .375rem 0 rgba(0, 0, 0, .5); 
    border-top: solid $blue-primary-mid 4px;
    
    .modal-close {
        text-indent: -9999px;
        height: 24px;
        width: 24px;
        background: url(../assets/Icon-cross-blue.svg) no-repeat;
        position: absolute;
        right: 32px;
        top: 32px;
        cursor: pointer;
    }
}
.modal-background {
    display: none;
}
.modal-background.active {
    width: 100%;
    height: 100%;
    display: block;
    background: rgba(51, 51, 51, .5);
    position: fixed;
    z-index: 6999;
}

/* FORM ELEMENTS WORKING */
/* Container */
.form-field-container {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    width: 100%;
    
    .radio-button {
        margin: 0 0 16px 0;
        
        .label {
            padding: 10px 15px;
        }
        .form-field {
            margin: 0;
            padding-left: 40px;
            
            &:focus {
                outline: none;
                border: solid 3px #254f90;
            }
        }
        
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    
    .field-label {
        width: 100%;
        display: block;
        font-size: 16px;
        padding: 0 0 16px 0;
        margin: 0;
    }
    
    .form-field {
        border: solid 1px #495c74;
        font-family: "Open Sans", sans-serif;
        font-weight: 400;
        font-style: normal;
        font-size: 16px;
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        margin-bottom: 8px;
    }
}



.form-field-container.validation-error .form-field {
    border: solid 3px #d2201e;
}

.form-field-container .radio-button input:not(:checked):focus + label,
.form-field-container .radio-button input:checked:focus + label {
    border: solid 3px #254f90;
}

.form-field-container .radio-button input:not(:checked):focus + label:before,
.form-field-container .radio-button input:checked:focus + label:before {
    border: 2.5px solid #254f90;
}

.form-field-container .radio-button input:disabled + label {
    outline: none;
    border: solid 1px #e2e2e2;
    background-color: #f5f5f5;
    color: #8e8e8e;
}

.form-field-container .radio-button input:checked,
.form-field-container .radio-button input:not(:checked) {
    position: absolute;
    left: -9999px;
}

.form-field-container .radio-button input:checked + label,
.form-field-container .radio-button input:not(:checked) + label {
    position: relative;
    cursor: pointer;
    line-height: 20px;
    display: flex;
    justify-content: flex-start;
    color: #333;
}

.form-field-container .radio-button input:not(:checked) + label:before {
    border: 2.5px solid #aab2c3;
    font-weight: normal;
}

.form-field-container .radio-button input:checked + label:before,
.form-field-container .radio-button input:not(:checked) + label:before {
    content: "";
    position: absolute;
    top: 8px;
    left: 10px;
    width: 18px;
    height: 18px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.form-field-container .radio-button input:checked + label:before {
    content: "⬤";
    border: 2.5px solid #254f90;
    font-weight: normal;
    position: absolute;
}

.form-field-container .radio-button input:checked + label {
    color: #254f90;
    font-weight: 600;
    border: solid 3px #254f90;
}

/* Error message */
.form-field-container .form-validation-message {
    color: #d2201e;
    display: none;
}

.form-field-container.validation-error .form-validation-message {
    display: block;
}